<template>
  <div class="message-view">
    <h2><span>Message</span> <span class="chinese"></span></h2>
    <p>.</p>
    <p class="tip"> v-html ,.</p>
    <div class="preview clearfix">
      <h3>.</h3>
      <p>,,3.</p>
      <div class="clearfix">
        <xc-button type="info" @click="msg('')"></xc-button>
        <xc-button type="error" @click="msg('<span style=&quot;color: #fc605d&quot;></span>')"></xc-button>
      </div>
      <br>
      <div class="code-wrapper clearfix">
        <input type="checkbox" id="code_button1" class="kakushi code-control-ck" checked>
        <div class="code-html full">
          <p class="code-head vue"><xc-icon type="vue"></xc-icon>  <label class="code-control" for="code_button1">&lt;<span>/</span>&gt;</label></p>
          <xc-code lang="html" :code="html1" line="18"></xc-code>
        </div>
      </div>
    </div>
    <br>
    <div class="preview clearfix">
      <h3>.</h3>
      <p>.</p>
      <div class="clearfix">
        <xc-button type="info" @click="open1('')"></xc-button>
        <xc-button type="success" @click="open2('')"></xc-button>
        <xc-button type="warning" @click="open3('')"></xc-button>
        <xc-button type="error" @click="open4('')"></xc-button>
      </div>
      <br>
      <div class="code-wrapper clearfix">
        <input type="checkbox" id="code_button2" class="kakushi code-control-ck" checked>
        <div class="code-html full">
          <p class="code-head vue"><xc-icon type="vue"></xc-icon>  <label class="code-control" for="code_button2">&lt;<span>/</span>&gt;</label></p>
          <xc-code lang="html" :code="html2" line="32"></xc-code>
        </div>
      </div>
    </div>
    <h3><span>Options</span></h3>
    <xc-doc :list="doc"></xc-doc>
  </div>
</template>

<script>
export default {
  data() {
    return {
      html1: `<template>
  <xc-button type="info" @click="msg('')"></xc-button>
  <xc-button type="error" @click="msg('<span style=&quot;color: #fc605d&quot;></span>')"></xc-button>
</template>

<script>
export default {
  methods: {
    msg(msg) {
      this.$msg(msg);
    }
  }
};
<\/script>`,
      html2: `<template>
  <xc-button type="info" @click="open1('')"></xc-button>
  <xc-button type="success" @click="open2('')"></xc-button>
  <xc-button type="warning" @click="open3('')"></xc-button>
  <xc-button type="error" @click="open4('')"></xc-button>
</template>

<script>
export default {
  methods: {
    open1(msg) {
      this.$msg.info(msg);
    },
    open2(msg) {
      this.$msg.success(msg);
    },
    open3(msg) {
      this.$msg.warning(msg);
    },
    open4(msg) {
      this.$msg.error(msg);
    }
  }
};
<\/script>`,
      doc: [
        {property: 'message', description: '', type: 'String', default: ''},
        {property: 'closed', description: '', type: 'Function', default: ''},
        {property: 'duration', description: '(),0', type: 'Number', default: '3000'}
      ]
    };
  },
  methods: {
    msg(msg) {
      this.$msg(msg);
    },
    open1(msg) {
      this.$msg.info(msg);
    },
    open2(msg) {
      this.$msg.success(msg);
    },
    open3(msg) {
      this.$msg.warning(msg);
    },
    open4(msg) {
      this.$msg.error(msg);
    }
  },
  mounted() {}
};
</script>

<style lang="scss">
  .message-view {
    padding: 20px;
    .preview {
      font-size: 12px;
    }
  }
</style>



