<style lang="scss">
  .calendar-page {
    .demo {
      position: relative;
      display: block;
      width: 400px;
      overflow: hidden;
    }
    .preview {
      padding: 0;
      font-size: 12px;
      code {
        height: 100%;
        padding: 1em;
      }
    }
  }
</style>

<template>
  <div class="calendar-page">
    <h2><span>Calendar</span> <span class="chinese"></span></h2>
    <p>.</p>
    <p>slot,. (,)</p>
    <p class="tip"> moment.js . NPM moment .</p>
    <p>
      <span class="demo">
        <xc-calendar :sp="spDays" @select="select" align="center"><xc-icon type="github" style="height: 24px; vertical-align: text-top;"></xc-icon> </xc-calendar>
      </span>
    </p>
    <div class="preview clearfix">
      <xc-code lang="javascript" :code="code" line="26"></xc-code>
    </div>
    <h3><span>Attributes</span></h3>
    <xc-doc :list="doc"></xc-doc>
    <h3><span>Events</span></h3>
    <xc-doc :list="eventsDoc" type="event"></xc-doc>
  </div>
</template>

<script>
export default {
  data() {
    return {
      spDays: ['2019-11-11'],
      doc: [
        {property: 'spDays', description: ',', type: 'Array', default: `[]<br>['2019-11-11']`},
        {property: 'align', description: ' [left, center]', type: 'String', default: `left`}
      ],
      eventsDoc: [
        {eventName: 'change', description: '', callbackParamet: 'YYYY, M'},
        {eventName: 'select', description: '', callbackParamet: 'YYYY-MM-DD'}
      ],
      code: `<template>
  <xc-calendar :sp="spDays" @select="select" align="center">
    <xc-icon type="github" style="height: 24px; vertical-align: text-top;"></xc-icon> 
  </xc-calendar>
</template>

<script>
  export default {
    data() {
      return {
        spDays: ['2019-11-11']
      };
    },
    methods: {
      select(d) {
        this.$msg.info(d);
      }
    }
  };
<\/script>`
    };
  },
  methods: {
    select(d) {
      this.$msg.info(d);
    }
  },
  mounted() {}
};
</script>



