  "users":[
    {"id":1,"name":""},
    {"id":2,"name":""},
    {"id":3,"name":""}
  ],
  "display":{
    "depts":true,
    "dutys":true,
    "groups":true,
    "users":true
  }
}`,
      data2: `{
  "depts": [
    { "id": 5, "name": "" }
  ],
  "dutys": [
    {
      "id": 1,
      "name": "1",
      "depts": [
        { "id": 2, "name": "1", "tid": "Jones" }
      ]
    },
    {
      "id": 2,
      "name": "2",
      "depts": [
        { "id": 1, "name": "1", "tid": "Williams" },
        { "id": 2, "name": "2", "tid": "Walker" },
        { "id": 3, "name": "3", "tid": "Moore" }
      ]
    }
  ],
  "users": [
    { "id": 1, "name": "" },
    { "id": 2, "name": "" },
    { "id": 3, "name": "" },
    { "id": 6, "name": "" }
  ],
  "display": {
    "depts": true,
    "dutys": true,
    "groups": true,
    "users": true
  }
}`,
      doc: [
        {property: 'data', description: ',Data1', type: 'Object', default: ''},
        {property: 'value', description: ',Data2', type: 'Object', default: ''},
        {property: 'disabled', description: '', type: 'Boolean', default: 'false'},
        {property: 'disableadd', description: '', type: 'Boolean', default: 'false'},
        {property: 'size', description: '()', type: 'Array', default: '[240, 100]'}
      ],
      eventsDoc: [
        {eventName: 'search', description: ',mock', callbackParamet: 'String: , callback: '},
        {eventName: 'follow', description: '/()', callbackParamet: 'Object: {id, name, follow}, callback: '},
        {eventName: 'change', description: '', callbackParamet: 'Object: '}
      ]
    };
  },
  methods: {
    search(key, callback) {
      $.get(host + 'cms-rest/manager/search', {name: key}, callback);
    },
    follow(data, callback) {
      $.get(host + 'cms-rest/manager/follow', data, callback);
    },
    change() {
      this.getValue();
    },
    getValue() {
      this.testValue = this.$refs.transfer.Value;
    }
  },
  mounted() {
    // $.get(host + 'cms-rest/manager', data => {
    //   this.testData = data;
    // });
    this.testData = demoData;
  }
};
</script>

<style lang="scss">
  .transfer-view {
    padding: 20px;
    .icon-arrow {
      float: left;
      margin-top: 184px;
    }
  }
</style>



