<template>
  <div class="components-index">
    <h2><span>npm </span> <span class="chinese" v-text="ver"></span></h2>
    <p>.()</p>
    <p class="tip">.babel-plugin-component,.</p>
    <h5></h5>
    <div class="preview clearfix">
      <xc-code lang="shell" :code="npm" line="3"></xc-code>
    </div>
    <br>
    <p> main.js .</p>
    <div class="preview clearfix">
      <xc-code lang="javascript" :code="js" line="15"></xc-code>
    </div>
    <br>
    <h5><del></del></h5>
    <p><del> https://element.eleme.cn/#/zh-CN/component/quickstart .<br>.()</del></p>
    <h5></h5>
    <p>,,,.</p>
    <p class="tip">,.</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ver: this.$root.version,
      npm: 'npm install shiroui -save',
      js: `import Vue from 'vue';
import ShiroUI from 'shiroui';
import 'shiroui/dist/index.css';
import App from './App.vue';

Vue.use(ShiroUI);

new Vue({
  el: '#app',
  render: h => h(App)
});`
    };
  },
  methods: {
  }
}
</script>

<style lang="scss">
  .components-index {
    .preview {
      padding: 0;
      font-size: 12px;
      code {
        height: 100%;
        padding: 1em 1em 0;
      }
    }
  }
</style>


