<style lang="scss">
  .font-mask {
    .demo {
      position: relative;
      display: block;
      width: 100%;
      padding-bottom: 50%;
      overflow: hidden;
      // background: url(../../../assets/demo0.jpg) no-repeat center / cover;
      &::after {
        content: '';
        position: absolute;
        top: 0; left: 0;
        width: 2000%;
        height: 1000%;
        background-size: 25% 50%;
        background-image: radial-gradient(ellipse,#fc605d, #fc605d, #fdbc40, #34c849, #d08ce0, #51baf2);
        animation: slantMove 60s cubic-bezier(.5, .3, .5, .6) infinite;
        opacity: .3;
        z-index: -1;
      }
    }
  }
  @keyframes slantMove {
    0% {transform: translate3d(0, 0, 0);}
    25% {transform: translate3d(-5%, -50%, 0);}
    50% {transform: translate3d(-30%, -65%, 0);}
    75% {transform: translate3d(-55%, -35%, 0);}
    100% {transform: translate3d(-75%, -50%, 0);}
  }
</style>

<template>
  <div class="font-mask">
    <h2><span>Mask</span> <span class="chinese"></span></h2>
    <p>.</p>
    <p>
      <span class="demo">
        <xc-mask text="Mask" color="#ffffff" :h="70" :scale="1.4" :opacity="[70, 20]"></xc-mask>
      </span>
    </p>
    <h3><span>Attributes</span></h3>
    <xc-doc :list="doc"></xc-doc>
  </div>
</template>

<script>
export default {
  data() {
    return {
      doc: [
        {property: 'text', description: '', type: 'String', default: ''},
        {property: 'color', description: ',(,)', type: 'Color', default: '#d3d3d3'},
        {property: 'h', description: '(0-100)', type: 'Number', default: '70'},
        {property: 'fz', description: ',()', type: 'String', default: 'auto'},
        {property: 'scale', description: ', fz ,', type: 'Number', default: '1'},
        {property: 'opacity', description: ',,', type: 'Array / Number', default: '[80, 60]'}
      ]
    };
  },
  methods: {},
  mounted() {}
};
</script>



