<template>
  <div class="icon">
    <h2><span>FlexTree</span> <span class="chinese"></span></h2>
    <p>.</p>
    <div class="preview clearfix">
      <xc-flextree :data="flextreeData"></xc-flextree>
    </div>
    <h3><span>Attributes</span></h3>
    <xc-doc :list="doc"></xc-doc>
  </div>
</template>

<script>
export default {
  data() {
    return {
      doc: [
        {property: 'data', description: '', type: 'Object', default: ''}
      ],
      flextreeData: {
        "name": "D3",
        "children":
        [
          {
            "name": "",
            "children": [
              {"name": "HTML & CSS"},
              {"name": "JavaScript"},
              {"name": "DOM"},
              {"name": "SVG"},
              {"name": "test"}
            ]
          }, {
            "name": "",
            "_children": [
              {"name": ""}
            ]
          }, {
            "name": "",
            "children": [
              {"name": ""},
              {"name": "test"},
              {"name": ""},
              {"name": ""},
              {
                "name": "",
                "children":
                [
                  {"name": ""},
                  {"name": ""},
                  {"name": ""}
                ]
              },
              {"name": ""},
              {"name": ""},
              {"name": ""}
            ],
            "left": true
          }, {
            "name": "",
            "left": true
          }, {
            "name": "",
            "children": [
              {"name": "1"},
              {"name": "2"},
              {"name": "3"}
            ]
          }
        ]
      }
    };
  },
  methods: {
  }
}
</script>

<style lang="scss">
  .icon {
    .preview {
      padding-bottom: .18rem;
    }
  }
</style>


