<template>
  <div class="timeline">
    <h2><span>TimeLine</span> <span class="chinese"></span></h2>
    <p>S. CSS</p>
    <div class="preview clearfix">
      <xc-timeline :list="timelinedata" :fontsize="12"></xc-timeline>
    </div>
    <h3><span>Attributes</span></h3>
    <xc-doc :list="doc"></xc-doc>
    <h3><span>Item Attributes</span></h3>
    <xc-doc :list="itemdoc"></xc-doc>
  </div>
</template>

<script>
export default {
  data() {
    return {
      timelinedata: [
        {id: 1, time: '2019-12-24', icon: 'vue', content: '12r23sdfwsfwfew'},
        {id: 2, time: '2019-12-25', icon: 'sass', content: ''},
        {id: 3, time: '2019-12-26', content: 'ewfwfwegerwwwwwwwww'},
        {id: 4, time: '2019-12-27', content: ''},
        {id: 5, time: '2019-12-28', content: ''},
        {id: 6, time: '2019-12-29', content: ''},
        {id: 7, time: '2019-12-30', content: ''},
        {id: 8, time: '2019-12-31', content: ''},
        {id: 9, time: '2020-01-01', content: 'vVVR'},
        {id: 10, time: '2020-01-02', content: ''},
        {id: 11, time: '2020-01-03', content: 'vVVR'},
        {id: 12, time: '2020-01-04', content: 'vVVR'},
        {id: 13, time: '2020-01-05', content: ''},
        {id: 14, time: '2020-01-06', content: ''},
        {id: 15, time: '2020-01-07', content: ''},
        {id: 16, time: '2020-01-08', content: 'vVVR'},
        {id: 17, time: '2020-01-09', content: 'qwqwq'}
      ],
      list: ['pic', 'del', 'txt', 'ok', 'warn', 'error', 'bar', 'plus', 'bilibili', 'github', 'trash', 'copy', 'html', 'sass', 'json', 'vue', 'left', 'right'],
      doc: [
        {property: 'count', description: '', type: 'Number', default: '4'},
        {property: 'fontsize', description: 'px', type: 'Number', default: '12'},
        {property: 'list', description: '', type: 'Array', default: '[Object]'}
      ],
      itemdoc: [
        {property: 'id', description: '', type: 'String/Number', default: ''},
        {property: 'time', description: '', type: 'String', default: ''},
        {property: 'content', description: '', type: 'String', default: ''},
        {property: 'icon', description: '()', type: 'String', default: ''},
        {property: 'img', description: '()', type: 'String', default: ''}
      ]
    };
  },
  methods: {
  }
}
</script>

<style lang="scss">
  .timeline {
    .preview {
      padding-bottom: .18rem;
      .item {
        float: left;
        width: 10%;
        min-width: 60px;
        padding: 10px;
        color: #666;
        text-align: center;
        svg {
          height: 36px;
        }
        span {
          display: block;
          line-height: 2;
        }
      }
    }
  }
</style>


